.widget.widget_search {

    form {
        @include mkdfRelativeHolderLayout();
        width: 86%;
        clear: both;

        > div {
            @include mkdfTableLayout();
            border: 1px solid #f8edf0;
            box-shadow: 0 1px 23.28px 0.72px rgba(255, 51, 102, 0.07);

            .search-holder-part {
                @include mkdfTableCellLayout();
                box-sizing: border-box;
                border-right: 1px solid #f8edf0;

                &:last-child {
                    border-right: none;
                }

                &.input-holder {
                    border-top-left-radius: 2px;
                    border-bottom-left-radius: 2px;

                    .mkdf-search-icon {
                        position: absolute;
                        left: 20px;
                        top: 50%;
                        @include mkdfTransform(translateY(-50%));
                        font-size: 14px;
                        color: $additional-text-color;
                        z-index: 2;
                        margin-top: 2px;
                    }
                }

                &.submit-holder {
                    position: relative;
                    border: none;
                    width: 56px;
                    text-align: right;
                    cursor: pointer;

                    &:before {
                        position: absolute;
                        content: "\e041";
                        font-family: "dripicons-v2" !important;
                        top: 50%;
                        left: 50%;
                        @include mkdfTransform(translate(-50%,-50%));
                        font-size: 22px;
                        color: #fff;
                        z-index: 999;
                    }

                    button {
                        height: 56px;
                        border-radius: 0;
                        padding: 21px 22px;
                        width: 100%;
                        font-size: 0;
                        background-color: #ff3366;
                        border-color: #ff3366;
                        border-style: unset;
                        cursor: pointer;
                        @include mkdfTransition(background-color .3s ease-in-out);

                        &:hover {
                            background-color: rgba(#ff3366, .9);
                            @include mkdfTransition(background-color .3s ease-in-out);
                        }
                    }

                }

                input {
                    -webkit-appearance: none;
                    outline: none;
                    margin: 0;
                    padding: 0 0 0 48px;
                    border-radius: 0;
                    border: 0;
                    font-size: 14px;
                    line-height: 56px;
                    cursor: auto;
                    width: 100%;

                    @include mkdfPlaceholder {
                        color: $additional-text-color;
                    }

                    &::-webkit-search-decoration,
                    &::-webkit-search-cancel-button,
                    &::-webkit-search-results-button,
                    &::-webkit-search-results-decoration {
                        display: none;
                    }
                }
            }
        }
    }
}