// widgets spacing
$widget-bottom-margin: 40px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;
$widget-title-margin: 0 0 15px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 9px;

// list items color
$widget-list-item-background-color: #ffffff;

//// list items border
//$widget-list-item-border-color: $default-border-color;
//$widget-list-item-border-width: 1px;
//$widget-list-item-border-style: solid;

// text
$widget-heading-color: $default-heading-color;
$widget-text-color: $additional-text-color;

//list item
$list-item-color: $widget-text-color;
$list-item-size: 16px;
$list-item-weight: 400;

// links
$widget-link-color: $widget-text-color;
$widget-link-hover-color: $first-main-color;
$widget-link-size: 16px;
$widget-link-weight: 400;

// specific widgets
$widget-search-form-height: 45px;
$widget-tag-cloud-height: 23px;

aside.mkdf-sidebar {
    z-index: 11;
}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    position: relative;
    @import 'widgets/default-wordpress-widgets-flat';

    .widget {
        .mkdf-widget-title-holder {
            text-align: left;

            .mkdf-widget-title {
                position: relative;
                display: inline-block;
                margin: 0 0 2px;
            }
        }

        &.widget_nav_menu ul.menu {
            > li.menu-item-has-children {
                > a {
                    position: relative;
                    font-size: 18px;
                    font-weight: 500;
                    color: #000;
                    @include mkdfTransition(color .2s ease-in-out);
                    padding-left: 20px;

                    &:hover {
                        color: $widget-link-hover-color;
                    }

                    > span {
                        &:before {
                            position: absolute;
                            content: "\e035";
                            font-family: "dripicons-v2" !important;
                            left: 0;
                            vertical-align: top;
                            padding-right: 5px;
                        }
                    }

                    &.mkdf-custom-menu-active {
                        > span {
                            &:before {
                                content: "\e024";
                                font-family: "dripicons-v2" !important;
                                vertical-align: top;
                                padding-right: 5px;
                            }
                        }
                    }
                }

                > ul.sub-menu {
                    display: none;
                    border-top: none;
                    margin-left: 23px;
                    padding-left: 0;

                    > li {
                        overflow: hidden;
                        padding: 6px 0;

                        &:first-child {
                            padding-top: 10px;
                        }


                        &:last-child {
                            padding-bottom: 10px;
                        }

                    > a {
                            display: inline;
                            overflow: hidden;
                            padding: 0;
                            position: relative;
                            color: $additional-text-color;

                            &:before {
                                @include mkdfTransform(scaleX(0));
                                transform-origin: 0 50%;
                            }

                            &:after {
                                @include mkdfTransform(scaleX(0));
                                opacity: 0;
                            }

                            &:hover {
                                color: $widget-link-hover-color;
                            }
                        }
                    }

                    .sub-menu {
                        padding-left: 15px;
                        box-sizing:border-box;
                    }
                }
            }

            li {

                a {
                    font-size: 14px;
                    font-weight: 400;
                    color: #000;
                    @include mkdfTransition(color .2s ease-in-out);
                    &:hover {
                        color: $widget-link-hover-color;
                    }
                }
            }

            > li {
                > a {
                    position: relative;
                    font-size: 18px;
                    font-weight: 500;
                    color: #000;
                    @include mkdfTransition(color .2s ease-in-out);
                    padding-left: 20px;

                    &:hover {
                        color: $widget-link-hover-color;
                    }
                }
            }

        }

        &.mkdf-resume-widget {
            box-sizing: border-box;

            .mkdf-resume-list-simple {

                background-color: transparent;
                border: none;
                border-radius: 0;
                padding: 0;
                .mkdf-rs-list-items-text-holder {
                    display: none;
                }

                .mkdf-rs-list-items-holder-inner {
                    display: block;
                    padding: 0;
                    width: 100%;

                    .mkdf-rsi-inner img {
                        border-radius: 0;
                    }
                }
            }

        }

        &.widget_text {
            .faq-sidebar-links {
                a {
                    color: $additional-text-color;
                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_featured_resumes {
            ul.resumes li.no_resumes_found.resume_featured a,
            ul.resumes li.resume.resume_featured a {
                background-color: transparent;
                @include mkdfTransition(background-color .2s ease-in-out);

                &:hover {
                    background-color: rgba($additional-text-color, 0.04);
                    @include mkdfTransition(background-color .2s ease-in-out);
                }
            }
        }

        ul.job_listings .listing-bookmarked h3::before,
        ul.resumes .listing-bookmarked h3::before {
            color: $first-main-color;
        }

        ul.resumes li.no_resumes_found a,
        ul.resumes li.no_resumes_found a,
        ul.resumes li.resume a,
        ul.resumes li.resume a {
            padding: 1em 15px;

            &:focus,
            &:hover {

                background-color: rgba($additional-text-color, 0.04);
                @include mkdfTransition(background-color .2s ease-in-out);
            }
        }
    }

}