/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;
    background-color: $widget-background-color;
    border: 0;
    color: $widget-text-color;
    font-size: $widget-text-size;
    line-height: $widget-line-height;

    &:last-child {
        margin-bottom: 0;
    }

    .mkdf-widget-title-holder {
        margin: $widget-title-margin;

        .mkdf-widget-title {
            margin: 0;
            color: $widget-heading-color;
        }

        .mkdf-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            color: $list-item-color;
            font-size: $list-item-size;
            font-weight: $list-item-weight;
            border: 0;
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 2px;
                margin: 0;
            }
            &:first-child {
                padding-top: 0;
            }

            .children {
                padding: 8px 0 0 15px;

                li:last-child {
                    padding-bottom: 0;
                }
            }


            a {
                color: $widget-link-color;
                font-size: $widget-link-size;
                font-weight: $widget-link-weight;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-list-item-padding;
            padding-left: 15px
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries {

        .mkdf-widget-title {
            margin-bottom: 0;
        }

        ul {

            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                }
            }
        }
    }

    &.widget_nav_menu ul.menu {
        > li {
            overflow: hidden;
            a {
                display: inline;
                overflow: hidden;
                padding: 0;
                position: relative;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }


        }
    }

    &.widget_recent_entries ul li a {
        &:before,
        &:after {
            opacity: 0;
        }
        &:hover {
            &:after,
            &:before {
                opacity:0;
            }
        }
    }



    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $first-main-color;
            background-color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {

    }

    // search post type
    &.mkdf-search-post-type-widget {
        .mkdf-post-type-search-field {
            border-radius: 3px;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 12px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            padding: 0 25px 0 20px;
            width: 100%; /* width of loading icon */

            @include mkdfTransition(color .2s ease-in-out);

            @include mkdfPlaceholder {
                color: $widget-text-color;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
                color: $default-heading-color;
            }
        }
    }

    // tag cloud
    &.widget_tag_cloud {
        .tagcloud {
            margin-top: 15px;
        }

        a {
            border-radius: 2px;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-weight: 400;
            line-height: $widget-tag-cloud-height;
            margin: 0 6px 7px 0;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // image gallery dropdown

    &.mkdf-image-gallery-widget {
        .mkdf-image-gallery a {
            padding: 0 !important //because of the default link style

        }
    }
}