// widgets spacing
$widget-bottom-margin: 45px;
$widget-title-margin: 0 0 15px;

// widget background color
$widget-background-color: transparent;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 8px;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #ffffff;
$widget-text-size: 12px;
$widget-line-height: 1.5em;

// list items color
$widget-list-item-background-color: #999999;
$list-item-color: $widget-text-color;
$list-item-size: 13px;
$list-item-weight: 400;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// links
$widget-link-color: #eeeeee;
$widget-link-hover-color: $first-main-color;
$widget-link-size: 13px;
$widget-link-weight: 400;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import 'widgets/default-wordpress-widgets-flat';

    &.mkdf-page-footer {

        .widget {
            margin: 0 0 35px;
            font-size: 14px;

            .mkdf-widget-title-holder .mkdf-widget-title {
                font-size: 19px;
                font-weight: 500;
                letter-spacing: -0.03em;
                line-height: 1.26em;
                margin-bottom: 34px;
            }

            &.widget_mkdf_twitter_widget .mkdf-twitter-widget.mkdf-twitter-standard li {
                margin-bottom: 0 !important;

                &:first-child {
                    .mkdf-twitter-icon {
                        top: 5px;
                    }
                }

                .mkdf-twitter-icon {
                    top: 19px;
                    color: #40c4ff;
                    left: -6px;

                    i {
                        font-size: 14px;
                    }
                }

                .mkdf-tweet-text {
                    font-size: 14px;
                    padding: 1px 0 0 23px;

                    a,
                    span {
                        color: #fff;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
            }

            a {
                color: #fff;

                &:hover {
                    color: $first-main-color;
                }
            }

            ul {
                li {
                    border-bottom: 0;

                }

                .sub-menu {
                    border: 0;
                }
            }

            p {
                font-size: 14px;
                color: #fff;
                line-height: 1.64em;
            }

            &.widget_pages,
            &.widget_archive,
            &.widget_categories,
            &.widget_meta,
            &.widget_recent_entries,
            &.widget_nav_menu {

                ul {

                    li {

                        a {

                            &:after {
                                color: #97a1a9;
                            }

                            &:hover {

                                &:after {
                                    color: $first-main-color;
                                }
                            }
                        }
                    }
                }
            }

            &.widget_nav_menu {
                margin: 0 0 15px;
                .mkdf-widget-title-holder {
                    .mkdf-widget-title {
                        margin-bottom: 36px;
                    }
                }

                ul {
                    li {
                        padding: 5px 0;

                        a {
                            display: inline;
                            overflow: hidden;
                            padding: 0;
                            position: relative;
                            padding-left: 15px;
                            font-size: 14px;
                            letter-spacing: -0.02em;

                            @include mkdfTransition(padding .2s ease, color .2s ease);

                            &:after {
                                display: inline-block;
                                font-family: "IonIcons";
                                position: absolute;
                                speak: none;
                                text-rendering: auto;
                                text-transform: none;
                                vertical-align: middle;
                                content: '\25CF';
                                top: 50%;
                                left: 0;
                                width: 20px;
                                color: inherit;
                                font-size: 9px;
                                line-height: inherit;

                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;

                                @include mkdfTransform(translateY(-50%));
                                @include mkdfTransition(left .2s cubic-bezier(0.54, 0.49, 0, 1.75));
                            }

                            &:hover {
                                padding-left: 15px;

                                &:after {
                                    left: 0;
                                }
                            }
                        }
                    }
                }
            }

            &.widget_media_image {
                margin: 0 0 15px;
            }

            &.widget_text {
                margin: 0 0 15px;
            }

            &.widget_search form>div {
                border: 0;

                .search-holder-part {
                    border-right: 0;
                }
            }

            &.mkdf-listing-widget {
                .mkdf-ls-list-simple {
                    .mkdf-ls-item-author-image {
                        width: 53px;
                    }

                    .mkdf-ls-content {
                        padding-left: 15px;

                        .mkdf-listing-title {
                            font-size: 14px;
                            margin: 5px 0 0;

                            + p {
                                display: none;
                            }
                        }

                        .mkdf-ls-adr-pin {
                            padding: 3px 4px 0 0;
                            a {
                                font-size: 10px;
                            }
                        }

                        .mkdf-ls-adr-city {
                            display: inline-block;
                            vertical-align: middle;
                            line-height: 22px;
                            .mkdf-city {
                                font-size: 12px;
                                vertical-align: middle;
                                top: 0;
                            }
                        }
                    }
                }
            }
        }

        a {
            color: #fff;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-footer-bottom-holder {
            .widget_search form>div {

                .search-holder-part {
                    input {
                        line-height: 57px;
                    }
                }
            }
        }
    }
}
