@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    position: relative;
    display: inline-block;
    width: calc(100% - 110px);
    float: right;
    vertical-align: middle;
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 6px 0 2px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            margin-right: 10px;
            font-size: 14px;
            font-weight: 500;
            text-transform: capitalize;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            position: relative;
            float: left;
            bottom: 2px;
            margin: 0 0 0 10px;
            font-size: 14px;
        }

        .mkdf-text-holder p {
            margin: 0 0 5px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    width: calc(100% - 110px);
    float: right;

    .mkdf-comment-form-inner {
        padding: 45px 0 108px;
    }
    
    .comment-reply-title {
        margin: 0 0 25px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }

    textarea#comment,
    .mkdf-comment-form-fields input#author,
    .mkdf-comment-form-fields input#email,
    .mkdf-comment-form-fields input#url {
        font-size: 15px;
        color: $additional-text-color;
        cursor: auto;
    }

    .form-submit {
        #submit_comment {
          font-size: 15px;
          text-transform: capitalize;
        }
    }
}

@include ipad-landscape {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-list {
			
			.children {
				padding: 0 0 0 61px;
			}
		}
	}
}

@include ipad-portrait {

    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
	        
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

			.mkdf-comment-date {
				margin: 0;
			}
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
	width: 100%;
	margin: 0 0 20px;
	padding: 14px 18px;
	font-family: inherit;
	font-size: 14px;
	line-height: 20px;
	font-weight: inherit;
	color: $default-heading-color;
	background-color: transparent;
	border: 1px solid $default-border-color;
	border-radius: 0;
	outline: 0;
	-webkit-appearance: none;
	cursor: pointer;
	box-sizing: border-box;
	@include mkdfTransition(border-color 0.2s ease-in-out);
	
	&:focus {
		color: darken($default-heading-color,20%);
	}
	
	@include mkdfPlaceholder {
		color: inherit;
	}
}

#respond textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonMediumParams();
	@include mkdfButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		@include mkdfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.mkdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
        padding: 10px;
        margin: -10px;
    }
	
	&.mkdf-slider-is-moving {
		
		.owl-item {
			
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.mkdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include mkdfTransform(translateY(-50%));
			@include mkdfTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 32px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 11px;
				height: 11px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: #e8e9ea;
				border: 0;
				box-sizing: border-box;
				@include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $first-main-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.mkdf-404-page {

    &.mkdf-wrapper {
        padding: 0 !important;
    }
    
    .mkdf-content {
        height: 100vh;
	
	    .admin-bar & {
		    height: calc(100vh - 32px);
	    }
    }

    .mkdf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .mkdf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
	    text-align: center;

        .mkdf-404-title {
            margin: 0;
            display: none;
        }
	
	    .mkdf-404-subtitle {
            margin: 35px 0 0;
            font-weight: 400;
            padding: 0 10%;
        }
	    
	    .mkdf-404-text {
		    margin: 26px 0 0;
            padding: 0 21%;
	    }

        .mkdf-btn {
			margin: 38px 0 0;
	
	        &.mkdf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
	
	@include phone-landscape {
		
		.mkdf-content {
			height: auto;
			margin: 0 !important;
			
			.admin-bar & {
				height: auto;
			}
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
.mkdf-footer-light-skin-btt {
    #mkdf-back-to-top {
        >span{
            background-color: #31373f;
            border-color: #31373f;
        }
    }
}

.mkdf-footer-dark-skin-btt {
    #mkdf-back-to-top {
        >span{
            background-color: #f4f5f6;
            border-color: #f4f5f6;
            color: #585c62;
        }
    }
}

#mkdf-back-to-top {
    position: fixed;
    width: 59px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    overflow:hidden;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 56px;
        height: 56px;
        line-height: 65px;
        border-radius: 50%;
        font-size: 24px;
        text-align: center;
        color: #fff;
        overflow: hidden;


        > .mkdf-icon-stack-inner {
            position: relative;
            @include mkdfTransform(rotate(-45deg) translate(3px,1px) scale(1) rotateX(0deg));
            @include mkdfTransition(transform .5s cubic-bezier(0.65, -0.88, 0.93, 1.18));
        }

        &.mkdf-back-to-top-inner {
            position:absolute;
            top:0;
            left:0;
            font-size:16px;
            line-height:56px;
            height:56px;
            background:transparent;
            border:none;
            font-weight:700;
            opacity:0;
            text-transform:uppercase;
            @include mkdfTransform(translate3d(0,75%,0) rotateX(-90deg));
            @include mkdfTransition(all .5s cubic-bezier(0.65, -0.88, 0.93, 1.18) 0s);
        }
    }

    &:hover {
        > span {
            > .mkdf-icon-stack-inner {
                @include mkdfTransform(rotate(-45deg) translate3d(33px,-29px, 0px) scale(1.5) rotateX(90deg));
            }

            &.mkdf-back-to-top-inner { 
                opacity:1;
                @include mkdfTransform(translate3d(0,0,0) rotateX(0deg));
                @include mkdfTransition(all .5s cubic-bezier(0.65, -0.88, 0.93, 1.18) .25s);
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

