/* ==========================================================================
   #Custom Contact Forms styles - begin
   ========================================================================== */
.mkdf-subscribe {
    .mkdf-cf-name-column,
    .mkdf-cf-email-column,
    .mkdf-cf-website-column {
        position: relative;;
        input {
          border-radius: 30px;
        }

        &:before {
            position: absolute;
            top: 18%;
            left: 11%;
            font-size: 18px;
            font-family: "dripicons-v2" !important;
            color: $additional-text-color;
            z-index: 999;
        }
    }
    .mkdf-cf-name-column {
        &:before {
            content: "\e056";
        }
    }
    .mkdf-cf-email-column {
        &:before {
            content: "\e015";
        }
    }

    .mkdf-cf-website-column {
          &:before {
            content: "\e064";
          }
    }

    input[type=text],
    input[type=email] {
        padding: 14px 18px 14px 57px;
        background-color: #fff;
        color: $additional-text-color;
        cursor: auto;
    }

	span.wpcf7-not-valid-tip {
		color: $first-main-color;
		text-align: center;
	}
}

.mkdf-cf7-contact-us-button {
	margin-top: 20px;
}

.mkdf-newsletter-form {
    position: relative;

    &:before {
        position: absolute;
        content: "\e015";
        top: 18%; 
        left: 5.6%;
        font-size: 24px;
        font-family: "dripicons-v2" !important;
        color: $additional-text-color;
        z-index: 999;
    }

    input[type=email] {
        position: relative;
        background-color: #fff;
        border-radius: 50px;
        padding: 26px 18px 30px 88px;
        font-size: 18px;

		&:focus {
			box-shadow: 0 1px 23.28px 5.72px rgba(255,51,102,.07);
		}
    }

    input[type=submit] {
        position: absolute;
        top: 7%;
        right: 1%;
        padding: 16px 65px;
    }
}

/* ==========================================================================
   #Custom Contact Forms styles - end
   ==========================================================================*/