/* ==========================================================================
   Blog List widget style - begin
   ========================================================================== */

.widget {

    &.mkdf-blog-list-widget {
        margin: 0 0 20px;

        .mkdf-bl-simple {

            .mkdf-bli-content {
                padding-left: 0px;
                padding-top: 0;
            }

            ul {
                padding-top: 7px;
            }

        }
        .mkdf-post-image {
            width: 85px;
            overflow: hidden;
        }

        .mkdf-post-title {
            font-weight: 500;
            line-height: 1em;

            a {
                color: inherit !important; //important rule to override default widget style in all cases
                font-weight: inherit !important; //important rule to override default widget style in all cases

                &:hover {
                    color: $first-main-color !important;
                }
            }
        }

        .mkdf-post-info-date {
            margin: 0 !important;


            .mkdf-post-info-date-icon {
                vertical-align: middle;
                margin-right: 3px;
            }
        }

        .mkdf-bl-simple .mkdf-bli-content .mkdf-post-info-date a {
            vertical-align: top;
            font-size: 14px;
            color: $additional-text-color;
            font-weight: 400;

            &:hover {
                color: $first-main-color !important;
            }
        }

        .mkdf-bl-simple .mkdf-post-image {
            width: 81px;

            &:hover {
                + .mkdf-bli-content> .mkdf-post-title>a {
                    color: $first-main-color !important;
                }
            }
        }
    }
}

/* ==========================================================================
   Blog List widget style - end
   ========================================================================== */